<template>
	<div id="content">
	    <div v-if="preLoader == true" class="spinner-overlay show">
	      <span class="text-load" style="font-family: 'Product sans'">
	        Harap tunggu.
	      </span>
	      <div class="lds-roller">
	        <div></div><div></div><div></div><div></div><div></div><div></div><div></div><div></div>
	      </div>
	    </div>
	    <div v-if="preLoader == false" class="spinner-overlay"></div>
		<!-- Header -->
		<div class="header bg-gradient-primary" style="height: 200vh">
		  <div class="container-fluid">
		    <div class="header-body">
		      	
		    </div>
		  </div>
		</div>
		<!-- end header -->
		<div class="container-fluid" style="margin-top: -180vh">
			<div class="row">
				<div class="col-md-9"></div>
				<div class="col-md-3">
					<div class="form-group">
						<select id="jenis_dataset" class="form-control" @change="datasetOnChange($event)" v-model="jenis_dataset">
							<option value="data_asli">Dataset asli</option>
							<option value="kelas_25">Jumlah kelas 25</option>
							<option value="kelas_50">Jumlah kelas 50</option>
							<option value="kelas_100">Jumlah kelas 100</option>
							<option value="kelas_200">Jumlah kelas 200</option>
						</select>
					</div>
				</div>
				<div class="col-md-3">
				    <div class="card card-stats mb-3">
						<div class="card-body">
							<div class="row">
								<div class="col">
									<h5 class="card-title text-muted mb-0">Akurasi</h5>
									<span class="h2 font-weight-bold mb-0">{{Math.ceil(pengujian['akurasi'])}}%</span>
								</div>
								<div class="col-auto">
									<div class="icon icon-shape bg-danger text-white rounded-circle shadow">
										<i class="ni ni-atom"></i>
									</div>
								</div>
							</div>
						</div>
				    </div>
				</div>

				<div class="col-md-3">
				    <div class="card card-stats mb-4">
						<div class="card-body">
							<div class="row">
								<div class="col">
									<h5 class="card-title text-muted mb-0">Jumlah Salah</h5>
									<span class="h2 font-weight-bold mb-0">{{pengujian['jumlah_salah']}}/{{pengujian['jumlah_data']}}</span>
								</div>
								<div class="col-auto">
									<div class="icon icon-shape bg-yellow text-white rounded-circle shadow">
										<i class="fas fa-chart-bar"></i>
									</div>
								</div>
							</div>
						</div>
				    </div>
				</div>

				<div class="col-md-6">
				    <div class="card card-stats mb-4">
						<div class="card-body">
							<div class="row">
								<div class="col">
									<h5 class="card-title text-muted mb-0">Terakhir Uji</h5>
									<span class="h2 font-weight-bold mb-0">{{pengujian['timestamp']}}</span>
								</div>
								<div class="col-auto">
									<button v-on:click="ujiKembali()" class="btn btn-primary">Uji Kembali</button>
								</div>
							</div>
						</div>
				    </div>
				</div>
			</div>
			<div class="row mb-3">
				<div class="col-md-12">
				    <div class="card">
				    	<div class="card-header border-0">
				    	  <h3 class="mb-0">Rincian</h3>
				    	</div>
						<div class="card-body">
							<div style="height: 250px;overflow: auto">
								<table class="table align-items-center table-flush">
									<thead class="thead-light">
									  <tr>
									    <th scope="col">Kelas Sebenarnya</th>
									    <th scope="col">Nama Citra</th>
									    <th scope="col">Status</th>
									    <th scope="col">Kelas Prediksi</th>
									    <th scope="col">Keputusan</th>
									    <th scope="col">Persentase</th>
									    <th scope="col">Detail</th>
									  </tr>
									</thead>
									<tbody v-for="value in pengujian['rincian']">
										<tr>
											<td>{{value['testing']}}</td>
											<td>{{value['citra']}}</td>
											<td>
												<span v-if="value['status']=='Teridentifikasi'" class="badge badge-success">{{value['status']}}</span>
												<span v-else class="badge badge-danger">{{value['status']}}</span>
											</td>
											<td>{{value['prediksi']}}</td>
											<td>
												<span v-if="value['keputusan']=='Salah'" class="badge badge-danger">{{value['keputusan']}}</span>
												<span v-else-if="value['keputusan']=='Benar'" class="badge badge-primary">{{value['keputusan']}}</span>
												<span v-else class="badge badge-warning">{{value['prediksi']}}</span>
											</td>
											<td>
												<span v-if="value['persentase']>0">{{value['persentase']}}%</span>
												<span v-else>{{value['persentase']}}</span>
											</td>
											<td>
												<button :data-id="value['citra']" v-on:click="showDetailKlasifikasi(value['citra'])" class="btn btn-sm btn-primary"><i class="fa fa-eye"></i></button>
											</td>
										</tr>
									</tbody>
								</table>
							</div>
						</div>
					</div>
				    <div v-if="showDetail" class="card mt-3">
				    	<div class="card-header border-0">
				    	  <h3 class="mb-0">Detail Hasil Klasfikasi Citra {{citraSelected}}</h3>
				    	</div>
						<div class="card-body">
							<div style="height: 250px;overflow: auto">
								<table class="table align-items-center table-flush">
									<thead class="thead-light">
									  <tr>
									    <th scope="col">Kelas</th>
									    <th scope="col">Persentase</th>
									  </tr>
									</thead>
									<tbody v-for="value in detailKlasifikasi">
										<tr>
											<td>{{value['nama']}}</td>
											<td>{{value['persentase']}}%</td>
										</tr>
									</tbody>
								</table>
							</div>
						</div>
					</div>
				</div>
			</div>
		</div>
	</div>
</template>
<script>
import axios from 'axios';
export default{
	name:'pengujian',
	data (){
		return{
			title:'Pengujian',
			pengujian:[],
			preLoader:false,
			jenis_dataset: 'data_asli',
			showDetail:false,
			detailKlasifikasi:[],
			citraSelected:''
		}
	},
	methods: {
		load(){
			axios.get(this.$parent.urlApi + 'pengujian.php?jenis_data=data_asli').then(res => {
				this.pengujian = res.data;
			}).catch ((err) => {
				console.log(err);
			}).finally(() => {

			})
		},
		ujiKembali(){
			this.preLoader = true;
			// console.log(this.$parent.urlApi + 'uji_kembali.php?jenis_data=' + this.jenis_dataset);
			axios.get(this.$parent.urlApi + 'uji_kembali.php?jenis_data=' + this.jenis_dataset).then(res => {
				this.pengujian = res.data;
				this.preLoader = false;
				// this.$router.push({name:'pengujian'})
			}).catch ((err) => {
				console.log(err);
			}).finally(() => {

			})
		},
		datasetOnChange(event){
			// console.log(event.target.value);
			this.updateData(event.target.value);
		},
		updateData(jenis_data){
			axios.get(this.$parent.urlApi + 'pengujian.php?jenis_data=' + jenis_data).then(res => {
				this.pengujian = res.data;
			}).catch ((err) => {
				console.log(err);
			}).finally(() => {

			})
		},
		showDetailKlasifikasi(id){
			console.log(id)
			this.showDetail = true
			this.citraSelected = id
			
			for (var i = 0; i < this.pengujian['rincian'].length; i++) {
				if(this.pengujian['rincian'][i]['citra'] == id){
					this.detailKlasifikasi = this.pengujian['rincian'][i]['detail']
				}
			}
			console.log(this.detailKlasifikasi)
		}
	},
	mounted(){
		this.load();
		this.$parent.headerTitle = this.title;
		this.$parent.routerPath = this.$route.name;
		document.title = this.title + ' | ' + this.$parent.title;
	}
}
</script>
<style scoped>
  .spinner-overlay.show{
    display: block
  }
  .spinner-overlay{
    position: fixed;
     z-index: 999;
    background: linear-gradient(87deg, #5e72e4 0, #825ee4 100%) !important;
    height: 100%;
    width: 100%;
    display: none;
  }
  .text-load{
    position: absolute;
    color: white;
    top:35%;
    left:39%;
  }
 .lds-roller {
  position: absolute;
   display: inline-block;
   top:40%;
   left:40%;
   width: 80px;
   height: 80px;
 }
 .lds-roller div {
   animation: lds-roller 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
   transform-origin: 40px 40px;
 }
 .lds-roller div:after {
   content: " ";
   display: block;
   position: absolute;
   width: 7px;
   height: 7px;
   border-radius: 50%;
   background: #fff;
   margin: -4px 0 0 -4px;
 }
 .lds-roller div:nth-child(1) {
   animation-delay: -0.036s;
 }
 .lds-roller div:nth-child(1):after {
   top: 63px;
   left: 63px;
 }
 .lds-roller div:nth-child(2) {
   animation-delay: -0.072s;
 }
 .lds-roller div:nth-child(2):after {
   top: 68px;
   left: 56px;
 }
 .lds-roller div:nth-child(3) {
   animation-delay: -0.108s;
 }
 .lds-roller div:nth-child(3):after {
   top: 71px;
   left: 48px;
 }
 .lds-roller div:nth-child(4) {
   animation-delay: -0.144s;
 }
 .lds-roller div:nth-child(4):after {
   top: 72px;
   left: 40px;
 }
 .lds-roller div:nth-child(5) {
   animation-delay: -0.18s;
 }
 .lds-roller div:nth-child(5):after {
   top: 71px;
   left: 32px;
 }
 .lds-roller div:nth-child(6) {
   animation-delay: -0.216s;
 }
 .lds-roller div:nth-child(6):after {
   top: 68px;
   left: 24px;
 }
 .lds-roller div:nth-child(7) {
   animation-delay: -0.252s;
 }
 .lds-roller div:nth-child(7):after {
   top: 63px;
   left: 17px;
 }
 .lds-roller div:nth-child(8) {
   animation-delay: -0.288s;
 }
 .lds-roller div:nth-child(8):after {
   top: 56px;
   left: 12px;
 }
 @keyframes lds-roller {
   0% {
     transform: rotate(0deg);
   }
   100% {
     transform: rotate(360deg);
   }
 }
 .table th, .table td {
     padding: 0.5rem;
 }
</style>


