import Vue from 'vue'
import Router from 'vue-router'

import landing from '@/components/landing'
import dashboard from '@/components/dashboard'
import dataset from '@/components/dataset'
import model from '@/components/model'
import training from '@/components/training'
import identifikasi from '@/components/identifikasi'
import akuisisi from '@/components/akuisisi'
import identifikasi_citra from '@/components/identifikasi-citra'
import hasil_identifikasi from '@/components/hasil_identifikasi'
import petunjuk from '@/components/petunjuk'
import about from '@/components/about'
import pengujian from '@/components/pengujian'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'dashboard',
      component: dashboard
    },
    {
      path: '/dataset',
      name: 'dataset',
      component: dataset
    },
    {
      path: '/training',
      name: 'training',
      component: training
    },
    {
	  path: '/identifikasi',
      name: 'identifikasi',
      component: identifikasi
    },
    {
	  path: '/identifikasi-citra',
      name: 'identifikasi-citra',
      component: identifikasi_citra
    },
    {
	  path: '/akuisisi',
      name: 'akuisisi',
      component: akuisisi
    },
    {
    path: '/model',
      name: 'model',
      component: model
    },
    {
    path: '/hasil-identifikasi',
      name: 'hasil-identifikasi',
      component: hasil_identifikasi,
      props: true
    },
    {
    path: '/petunjuk',
      name: 'petunjuk',
      component: petunjuk
    },
    {
    path: '/about',
      name: 'about',
      component: about
    },
    {
    path: '/pengujian',
      name: 'pengujian',
      component: pengujian
    }
  ]
})


