<template>
	<div id="content">
		<div v-if="preLoader == true" class="spinner-overlay show">
		  <span class="text-load" style="font-family: 'Product Sans'">
		    Sedang memproses.
		  </span>
		  <div class="lds-roller">
		    <div></div><div></div><div></div><div></div><div></div><div></div><div></div><div></div>
		  </div>
		</div>
		<div v-if="preLoader == false" class="spinner-overlay"></div>
		<!-- Header -->
		<div class="header bg-gradient-primary pb-8 pt-5 pt-md-8">
		  <div class="container-fluid">
		    <div class="header-body">
		      
		    </div>
		  </div>
		</div>
		<!-- end header -->
		<div class="container-fluid mt--7">
		  <div class="row">
		  	<div class="col-8">
		  		<div class="card bg-secondary shadow">
		  		  <div class="card-header bg-white border-0">
		  		    <h3 class="mb-0">Input Citra</h3>
		  		  </div>
		  		  <div class="card-body">
		  		  	<div class="pl-lg-4">
		  		  	  <div class="row">
		  		  	    <div class="col-6">
		  		  	      <div class="form-group">
		  		  	        <label class="form-control-label" for="input-username">Citra</label>
		  		  	        <input type="file" id="image" name="image" class="form-control form-control-alternative" ref="image" v-on:change="onChangeFileUpload()">
		  		  	      </div>

		  		  	      <div class="custom-control custom-control-alternative custom-checkbox">
		  		  	        <input class="custom-control-input" id="imgproc" name="imgproc" ref="imgproc" type="checkbox" v-on:change="onChangeImgProc()">
		  		  	        <label class="custom-control-label" for="imgproc">
		  		  	          <span class="text-muted">Tampilkan <i>image processing</i></span>
		  		  	        </label>
		  		  	      </div>

		  		  	    </div>
		  		  	    <div class="col-6">
		  		  	      <div class="form-group">
		  		  	    	<label class="form-control-label" style="visibility: hidden;">Identifikasi</label>
		  		  	    	<button v-on:click="submitForm()" name="submit" class="form-control btn btn-primary">Identifikasi</button>
		  		  	      </div>
		  		  	    </div>
		  		  	  </div>
		  		  	</div>
		  		  </div>
		  		</div>
		  	</div>
		    <div class="col-4">
		    	<div class="card bg-default shadow">
		    	  <div class="card-header bg-transparent border-0" style="background:#1c345d!important">
		    	    <h3 class="mb-0 text-white">Contoh</h3>
		    	  </div>
		    	  <div class="card-body">
		    	  	<img :src="contoh_image" style="width: 100%">
					<!-- <p>Citra yang diinput harus mengikuti ketentuan berikut:</p>
					<ul>
						<li>Citra berskala 1:1 (square)</li>
						<li>Hanya menampilkan garis tangan</li>
						<li>Format .png atau .jpg atau .bmp</li>
					</ul> -->
		    	  </div>
		    	</div>
		    </div>
		  </div>
		  <!-- Footer -->
		  <footer class="footer">
		    <div class="row align-items-center justify-content-xl-between">
		      <div class="col-xl-6">
		        <div class="copyright text-center text-xl-left text-muted">
		          &copy; 2020 <a href="" class="font-weight-bold ml-1" target="_blank">Putu Jhonarendra</a>
		        </div>
		      </div>
		    </div>
		  </footer>
		</div>
	</div>
</template>
<script>
import axios from 'axios'
export default{
	name:'identifikasi',
	data (){
		return{
			title:'Identifikasi',
			contoh_image: require('../assets/images/palmprint_contoh.jpg'),
			image: '',
			imgproc: false,
			preLoader: false,
			hasilPredict:[]
		}
	},
	methods: {
		submitForm(){
			let formData = new FormData();
			formData.append('image', this.image);
			formData.append('imgproc', this.imgproc);
			this.preLoader = true;

			axios.post(this.$parent.urlApi + 'identifikasi.php', formData,
				{
					headers: {
						'Content-Type': 'multipart/form-data'
					}
				}).then(function(data){
					console.log(data.data);
					self.hasilPredict = data.data;
				}).finally(() => {
					this.preloader = false;
					this.$router.push({name:'hasil-identifikasi', params:{image: self.hasilPredict.image}});
				});
		},
		onChangeFileUpload(){
			this.image = this.$refs.image.files[0];
		},
		onChangeImgProc(){
			this.imgproc = this.$refs.imgproc.checked;
		}
	},
	mounted(){
		this.$parent.headerTitle = this.title;
		this.$parent.routerPath = this.$route.name;
		document.title = this.title + ' | ' + this.$parent.title;
		console.log()
	}
}
</script>
<style scoped>
  .spinner-overlay.show{
    display: block
  }
  .spinner-overlay{
    position: fixed;
     z-index: 999;
    background: linear-gradient(87deg, #5e72e4 0, #825ee4 100%) !important;
    height: 100%;
    width: 100%;
    display: none;
  }
  .text-load{
    position: absolute;
    color: white;
    top:35%;
    left:37.5%;
  }
 .lds-roller {
  position: absolute;
   display: inline-block;
   top:40%;
   left:40%;
   width: 80px;
   height: 80px;
 }
 .lds-roller div {
   animation: lds-roller 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
   transform-origin: 40px 40px;
 }
 .lds-roller div:after {
   content: " ";
   display: block;
   position: absolute;
   width: 7px;
   height: 7px;
   border-radius: 50%;
   background: #fff;
   margin: -4px 0 0 -4px;
 }
 .lds-roller div:nth-child(1) {
   animation-delay: -0.036s;
 }
 .lds-roller div:nth-child(1):after {
   top: 63px;
   left: 63px;
 }
 .lds-roller div:nth-child(2) {
   animation-delay: -0.072s;
 }
 .lds-roller div:nth-child(2):after {
   top: 68px;
   left: 56px;
 }
 .lds-roller div:nth-child(3) {
   animation-delay: -0.108s;
 }
 .lds-roller div:nth-child(3):after {
   top: 71px;
   left: 48px;
 }
 .lds-roller div:nth-child(4) {
   animation-delay: -0.144s;
 }
 .lds-roller div:nth-child(4):after {
   top: 72px;
   left: 40px;
 }
 .lds-roller div:nth-child(5) {
   animation-delay: -0.18s;
 }
 .lds-roller div:nth-child(5):after {
   top: 71px;
   left: 32px;
 }
 .lds-roller div:nth-child(6) {
   animation-delay: -0.216s;
 }
 .lds-roller div:nth-child(6):after {
   top: 68px;
   left: 24px;
 }
 .lds-roller div:nth-child(7) {
   animation-delay: -0.252s;
 }
 .lds-roller div:nth-child(7):after {
   top: 63px;
   left: 17px;
 }
 .lds-roller div:nth-child(8) {
   animation-delay: -0.288s;
 }
 .lds-roller div:nth-child(8):after {
   top: 56px;
   left: 12px;
 }
 @keyframes lds-roller {
   0% {
     transform: rotate(0deg);
   }
   100% {
     transform: rotate(360deg);
   }
 }
</style>


