<template>
  <div id="content">
    <div v-if="preLoader == true" class="spinner-overlay show">
      <span class="text-load" style="font-family: 'Product sans'">
        Harap tunggu. Sedang berlangsung proses pelatihan.
      </span>
      <div class="lds-roller">
        <div></div><div></div><div></div><div></div><div></div><div></div><div></div><div></div>
      </div>
    </div>
    <div v-if="preLoader == false" class="spinner-overlay"></div>
    <!-- Header -->
    <div class="header bg-gradient-primary pb-8 pt-5 pt-md-8">
      <div class="container-fluid">
        <div class="header-body">
          
        </div>
      </div>
    </div>
    <!-- end header -->

    <div class="container-fluid mt--7">
      <div class="row">
        <div class="col-8">
          <div class="card bg-secondary shadow">
            <div class="card-header bg-white border-0">
              <div class="row align-items-center">
                <div class="col-8">
                  <h3 class="mb-0">Training</h3>
                </div>
                <div class="col-4 text-right">
                  <a v-on:click="submitForm()" class="btn btn-primary text-white">Mulai</a>
                </div>
              </div>
            </div>
            <div class="card-body">
              <h6 class="heading-small text-muted mb-4">Informasi Training</h6>
              <div class="pl-lg-4">
                <div class="row">
                  <div class="col-lg-6">
                    <div class="form-group">
                      <label class="form-control-label" for="jumlah_kelas">Jumlah Kelas</label>
                      <input type="number" id="jumlah_kelas" class="form-control form-control-alternative" placeholder="Username" :value="dataset.length" readonly="" ref="jumlah_kelas" name="jumlah_kelas">
                    </div>
                  </div>
                  <div class="col-lg-6">
                    <div class="form-group">
                      <label class="form-control-label" for="jumlah_epoch">Jumlah Epoch</label>
                      <input type="number" name="jumlah_kelas" id="jumlah_epoch" class="form-control form-control-alternative" ref="epoch" v-model="epoch" required="">
                    </div>
                  </div>
                </div>
                <div class="row">
                  <div class="col-lg-6">
                    <div class="form-group">
                      <label class="form-control-label" for="batch_size">Batch Size</label>
                      <input type="number" name="batch_size" id="batch_size" class="form-control form-control-alternative" ref="batch_size" v-model="batch_size" required="">
                    </div>
                  </div>
                  <div class="col-lg-6">
                    <div class="form-group">
                      <label class="form-control-label" for="batch_size">Validation Split</label>
                      <input type="text" name="validation_split" id="validation_split" class="form-control form-control-alternative" ref="validation_split" v-model="validation_split" required="">
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <!-- Footer -->
      <footer class="footer">
        <div class="row align-items-center justify-content-xl-between">
          <div class="col-xl-6">
            <div class="copyright text-center text-xl-left text-muted">
              &copy; 2020 <a href="" class="font-weight-bold ml-1" target="_blank">Putu Jhonarendra</a>
            </div>
          </div>
        </div>
      </footer>
    </div>
  </div>
</template>
<script>
import axios from 'axios';
 export default{
  name:'training',
  data (){
   return{
    title:'Training',
    preLoader:false,
    dataset:[],
    epoch:50,
    batch_size:32,
    validation_split:0.25
   }
  },
  mounted(){
  	this.load();
  	this.$parent.headerTitle = this.title;
  	this.$parent.routerPath = this.$route.name;
  	document.title = this.title + ' | ' + this.$parent.title;
  },
  methods:{
  	load(){
  		axios.get(this.$parent.urlApi + 'dataset.php').then(res => {
  			this.dataset = res.data;
  		}).catch ((err) => {
  			console.log(err);
  		}).finally(() => {
  			// this.preLoader = false
  		})
  	},
    submitForm(){
      this.preLoader = true;
      let formData = new FormData();
      formData.append('epoch', this.epoch);
      formData.append('validation_split', this.validation_split);
      formData.append('batch_size', this.batch_size);

      axios.post(this.$parent.urlApi + 'training.php', formData,
        {
          headers: {
            'Content-Type': 'multipart/form-data'
          }
        }).then(function(data){
          // this.preLoader = false;
          // console.log(data.data.message);
        }).finally(() => {
          this.$router.push({name:'model'})
        });
    }
  }
 }
</script>
<style scoped>
  .spinner-overlay.show{
    display: block
  }
  .spinner-overlay{
    position: fixed;
     z-index: 999;
    background: linear-gradient(87deg, #5e72e4 0, #825ee4 100%) !important;
    height: 100%;
    width: 100%;
    display: none;
  }
  .text-load{
    position: absolute;
    color: white;
    top:35%;
    left:28.5%;
  }
 .lds-roller {
  position: absolute;
   display: inline-block;
   top:40%;
   left:40%;
   width: 80px;
   height: 80px;
 }
 .lds-roller div {
   animation: lds-roller 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
   transform-origin: 40px 40px;
 }
 .lds-roller div:after {
   content: " ";
   display: block;
   position: absolute;
   width: 7px;
   height: 7px;
   border-radius: 50%;
   background: #fff;
   margin: -4px 0 0 -4px;
 }
 .lds-roller div:nth-child(1) {
   animation-delay: -0.036s;
 }
 .lds-roller div:nth-child(1):after {
   top: 63px;
   left: 63px;
 }
 .lds-roller div:nth-child(2) {
   animation-delay: -0.072s;
 }
 .lds-roller div:nth-child(2):after {
   top: 68px;
   left: 56px;
 }
 .lds-roller div:nth-child(3) {
   animation-delay: -0.108s;
 }
 .lds-roller div:nth-child(3):after {
   top: 71px;
   left: 48px;
 }
 .lds-roller div:nth-child(4) {
   animation-delay: -0.144s;
 }
 .lds-roller div:nth-child(4):after {
   top: 72px;
   left: 40px;
 }
 .lds-roller div:nth-child(5) {
   animation-delay: -0.18s;
 }
 .lds-roller div:nth-child(5):after {
   top: 71px;
   left: 32px;
 }
 .lds-roller div:nth-child(6) {
   animation-delay: -0.216s;
 }
 .lds-roller div:nth-child(6):after {
   top: 68px;
   left: 24px;
 }
 .lds-roller div:nth-child(7) {
   animation-delay: -0.252s;
 }
 .lds-roller div:nth-child(7):after {
   top: 63px;
   left: 17px;
 }
 .lds-roller div:nth-child(8) {
   animation-delay: -0.288s;
 }
 .lds-roller div:nth-child(8):after {
   top: 56px;
   left: 12px;
 }
 @keyframes lds-roller {
   0% {
     transform: rotate(0deg);
   }
   100% {
     transform: rotate(360deg);
   }
 }
</style>


