  mounted(){
  	this.hasilPredict = this.$route.params.hasilPredict;
  	this.keputusan = hasilPredict.keputusan;
  	this.hasil = hasilPredict.hasil;
  	this.detail = hasilPredict.detail;
  	this.token = hasilPredict.token;
  	this.imgproc = hasilPredict.imgproc;
  	this.showDetail = false;

  	if(hasilPredict.imgproc=="true"){
  		this.setImage(hasilPredict.token);
  		this.akuisisi =  this.$parent.urlApi+'../../upload/'+hasilPredict.image;
  	}

  	this.$parent.headerTitle = this.title;
  	this.$parent.routerPath = this.$route.name;
  	document.title = this.title + ' | ' + this.$parent.title;
  }
 }
</script>
<style scoped>
 table td{
    color: #8898aa;
    text-transform: uppercase;
    font-size: 14px;
    font-weight: bold;
 }
</style>


