<template>
	<div id="content">
    <!-- Header -->
    <div class="header bg-gradient-primary pb-8 pt-5 pt-md-8">
      <div class="container-fluid">
        <div class="header-body">
          <!-- Card stats -->
          <div class="row">
            <div class="col-xl-3 col-lg-6">
              <div class="card card-stats mb-4 mb-xl-0">
                <div class="card-body">
                  <div class="row">
                    <div class="col">
                      <h5 class="card-title text-muted mb-0">Jumlah Kelas</h5>
                      <span class="h2 font-weight-bold mb-0">{{dashboardData.jumlahKelas}}</span>
                    </div>
                    <div class="col-auto">
                      <div class="icon icon-shape bg-danger text-white rounded-circle shadow">
                        <i class="fas fa-chart-bar"></i>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
            <div class="col-xl-3 col-lg-6">
              <div class="card card-stats mb-4 mb-xl-0">
                <div class="card-body">
                  <div class="row">
                    <div class="col">
                      <h5 class="card-title text-muted mb-0">Total Citra</h5>
                      <span class="h2 font-weight-bold mb-0">{{dashboardData.jumlahCitra}}</span>
                    </div>
                    <div class="col-auto">
                      <div class="icon icon-shape bg-warning text-white rounded-circle shadow">
                        <i class="fas fa-chart-pie"></i>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
            <div class="col-xl-3 col-lg-6">
              <div class="card card-stats mb-4 mb-xl-0">
                <div class="card-body">
                  <div class="row">
                    <div class="col">
                      <h5 class="card-title text-muted mb-0">Epoch</h5>
                      <span class="h2 font-weight-bold mb-0">{{dashboardData.epochTraining}}</span>
                    </div>
                    <div class="col-auto">
                      <div class="icon icon-shape bg-yellow text-white rounded-circle shadow">
                        <i class="fas fa-users"></i>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
            <div class="col-xl-3 col-lg-6">
              <div class="card card-stats mb-4 mb-xl-0">
                <div class="card-body">
                  <div class="row">
                    <div class="col">
                      <h5 class="card-title text-muted mb-0">Akurasi</h5>
                      <span class="h2 font-weight-bold mb-0">{{dashboardData.akurasi}}%</span>
                    </div>
                    <div class="col-auto">
                      <div class="icon icon-shape bg-info text-white rounded-circle shadow">
                        <i class="fas fa-percent"></i>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <!-- end header -->
    <!-- content -->
    <div class="container-fluid mt--7">
      <div class="row">
        <div class="col-md-6">
          <div class="card bg-default shadow">
            <div class="card-header bg-transparent border-0">
              <div class="row align-items-center">
                <div class="col">
                  <h3 class="text-white mb-0">Dataset</h3>
                </div>
                <div class="col text-right">
                  <router-link to="/dataset" class="btn btn-sm btn-primary">Selengkapnya</router-link>
                </div>
              </div>
            </div>
            <div class="table-responsive">
              <table class="table align-items-center table-dark table-flush">
                <thead class="thead-dark">
                  <tr>
                    <th scope="col">ID</th>
                    <th scope="col">Nama</th>
                    <th scope="col">Jumlah Citra</th>
                  </tr>
                </thead>
                <tbody v-for="data in dataset.slice(0,5)" :key="data.id_dataset">
                  <tr>
                    <td>{{data.id_dataset}}</td>
                    <td>{{data.nama}}</td>
                    <td>{{data.jumlah_citra}} citra</td>
                  </tr>
                </tbody>
              </table>
            </div>
          </div>
        </div>
        <div class="col-md-6">
          <div class="card shadow">
            <div class="card-header border-0">
              <div class="row align-items-center">
                <div class="col">
                  <h3 class="mb-0">History Training Model</h3>
                </div>
                <div class="col text-right">
                  <router-link to="/model" class="btn btn-sm btn-primary">Selengkapnya</router-link>
                </div>
              </div>
            </div>
            <img :src="acc">
          </div>
        </div>
      </div>

      <!-- Footer -->
      <footer class="footer">
        <div class="row align-items-center justify-content-xl-between">
          <div class="col-xl-6">
            <div class="copyright text-center text-xl-left text-muted">
              &copy; 2020 <a href="" class="font-weight-bold ml-1" target="_blank">Putu Jhonarendra</a>
            </div>
          </div>
        </div>
      </footer>
    </div>
    <!-- end content -->

  </div>
</template>
<script>
  import axios from 'axios'
 export default{
  name:'dashboard',
  data (){
   return{
    title:'Dashboard',
    dashboardData:[],
    dataset:[],
    acc:'',
   }
  },
  mounted(){
    this.loadDataset();
    this.loadDataDashboard();
    this.readToken();
    this.$parent.headerTitle = this.title;
    this.$parent.routerPath = this.$route.name;
    document.title = this.title + ' | ' + this.$parent.title;
  },
  methods:{
    loadDataset(){
      axios.get(this.$parent.urlApi + 'dataset.php').then(res => {
        this.dataset = res.data;
        this.preLoader = false;
      }).catch ((err) => {
        console.log(err);
      }).finally(() => {
        this.preLoader = false
      })
    },
    loadDataDashboard(){
      axios.get(this.$parent.urlApi + 'dashboard.php').then(res => {
        this.dashboardData = res.data;
      }).catch ((err) => {
        console.log(err);
      }).finally(() => {
        this.preLoader = false
      })
    },
    readToken(){
      axios.get(this.$parent.urlApi + 'token.php').then(res => {
        this.token = res.data.training;
        this.acc = 'http://localhost/palmprint/model/'+res.data.training+'_acc.png';
      }).catch ((err) => {
        console.log(err);
      }).finally(() => {

      })
    }
  }
 }
</script>
<style scoped>
 
</style>



