<template>
	<div id="content">
		<!-- Header -->
		<div class="header bg-gradient-primary pb-8 pt-5 pt-md-8">
		  <div class="container-fluid">
		    <div class="header-body">
		      
		    </div>
		  </div>
		</div>
		<!-- end header -->
		<div class="container-fluid mt--7">
		  <div class="row">
		    <div class="col">
		      <div class="card shadow">
		        <div class="card-header border-0">
		        	<h6 class="text-uppercase text-light ls-1 mb-1">Dataset</h6>
		          <h3 class="mb-0">{{jmlDataset}} Data</h3>
		        </div>
		        <div class="table-responsive">
		          <table class="table align-items-center table-flush">
		            <thead class="thead-light">
		              <tr>
		                <th scope="col">ID</th>
		                <th scope="col">Nama</th>
		                <th scope="col">Jumlah citra</th>
		                <th scope="col">Dibuat pada</th>
		              </tr>
		            </thead>
		            <tbody v-for="data in displayedDataset" :key="data.id_dataset">
		            	<tr>
							<td>
								<span class="badge badge-primary" style="font-size: 20px">{{data.id_dataset}}</span>
							</td>
							<td>{{data.nama}}</td>
							<td>{{data.jumlah_citra}} citra</td>
							<td>{{time_elapsed_string(data.created_at)}}</td>
		            	</tr>
		            </tbody>
		          </table>
		        </div>
		        <div class="card-footer py-4">
                  <nav aria-label="...">
                    <ul class="pagination justify-content-end mb-0">
                      <li class="page-item">
                        <a class="page-link" v-if="page != 1" @click="page--">
                          <i class="fas fa-angle-left"></i>
                          <span class="sr-only">Previous</span>
                        </a>
                        <a class="page-link disabled" v-else="page == 1">
                          <i class="fas fa-angle-left"></i>
                          <span class="sr-only">Previous</span>
                        </a>
                      </li>
                      <li class="page-item">
                        <a class="page-link" @click="page++" v-if="page < pages.length">
                          <i class="fas fa-angle-right"></i>
                          <span class="sr-only">Next</span>
                        </a>
                        <a class="page-link" v-else="page >= pages.length">
                          <i class="fas fa-angle-right"></i>
                          <span class="sr-only">Next</span>
                        </a>
                      </li>
                    </ul>
                  </nav>
                </div>
		      </div>
		    </div>
		  </div>

		  <!-- Footer -->
		  <footer class="footer">
		    <div class="row align-items-center justify-content-xl-between">
		      <div class="col-xl-6">
		        <div class="copyright text-center text-xl-left text-muted">
		          &copy; 2020 <a href="" class="font-weight-bold ml-1" target="_blank">Putu Jhonarendra</a>
		        </div>
		      </div>
		    </div>
		  </footer>
		</div>
	</div>
</template>
<script>
import axios from 'axios'
export default{
	name:'dataset',
	data (){
		return{
			title:'Dataset',
			preLoader: true,
			dataset:[],
			jmlDataset : 0,
			page: 1,
			perPage: 5,
			pages: []
		}
	},
	mounted(){
		this.load();
		this.$parent.headerTitle = this.title;
		this.$parent.routerPath = this.$route.name;
		document.title = this.title + ' | ' + this.$parent.title;
		// console.log(this.pages);
	},
	methods:{
		load(){
			axios.get(this.$parent.urlApi + 'dataset.php').then(res => {
				this.dataset = res.data;
				this.preLoader = false;
				this.jmlDataset = this.dataset.length;
			}).catch ((err) => {
				console.log(err);
			}).finally(() => {
				this.preLoader = false
			})
		},
		setPages(){
			let numberOfPages = Math.ceil(this.dataset.length / this.perPage);
			for(let index = 1; index<= numberOfPages; index++){
				this.pages.push(index);
			}
		},
		paginate(dataset){
			let page = this.page;
			let perPage = this.perPage;
			let from = (page * perPage) - perPage;
			let to = (page * perPage);
			return dataset.slice(from, to);
		},
		time_elapsed_string(datetime){
			var current= new Date();

			var datetime = new Date(datetime);

			var msPerMinute = 60 * 1000;
			var msPerHour = msPerMinute * 60;
			var msPerDay = msPerHour * 24;
			var msPerMonth = msPerDay * 30;
			var msPerYear = msPerDay * 365;
			
			var elapsed = current - datetime;
			
			if (elapsed < msPerMinute) {
				return Math.round(elapsed/1000) + ' detik yang lalu';   
			} else if (elapsed < msPerHour) {
				return Math.round(elapsed/msPerMinute) + ' menit yang lalu';   
			} else if (elapsed < msPerDay ) {
				return Math.round(elapsed/msPerHour ) + ' jam yang lalu';   
			} else if (elapsed < msPerMonth) {
				return Math.round(elapsed/msPerDay) + ' hari yang lalu';
			} else if (elapsed < msPerYear) {
				return Math.round(elapsed/msPerMonth) + ' bulan yang lalu';
			} else {
				return Math.round(elapsed/msPerYear ) + ' tahun yang lalu';
			}
		}
	},
	computed: {
		displayedDataset(){
			return this.paginate(this.dataset);
		}
	},
	watch: {
		dataset(){
			this.setPages();
		}
	},
	filters: {
		trimwords(value){
			return value.split("").splice(0,20).join(" ") + '...';
		}
	}
}
</script>
<style scoped>
	#pagination{
		padding: 20px;
		float: right;
	}
 button.page-link{
 	display: inline-block;
 }
</style>


