<template>
	<div id="content">
		<!-- Header -->
		<div class="header bg-gradient-primary" style="height: 120vh">
		  <div class="container-fluid">
		    <div class="header-body">
		      
		    </div>
		  </div>
		</div>
		<!-- end header -->
		<div class="container-fluid" style="margin-top: -100vh">
			<div class="row">
				<div class="col-md-12">
					<div class="row">
						<div class="col-md-3">
						    <div class="card card-stats mb-3">
								<div class="card-body">
									<div class="row">
										<div class="col">
											<h5 class="card-title text-muted mb-0">Train_acc</h5>
											<span class="h2 font-weight-bold mb-0">{{model.acc}}%</span>
										</div>
										<div class="col-auto">
											<div class="icon icon-shape bg-danger text-white rounded-circle shadow">
												<i class="ni ni-atom"></i>
											</div>
										</div>
									</div>
								</div>
						    </div>
						</div>

						<div class="col-md-3">
						    <div class="card card-stats mb-4">
								<div class="card-body">
									<div class="row">
										<div class="col">
											<h5 class="card-title text-muted mb-0">Val_acc</h5>
											<span class="h2 font-weight-bold mb-0">{{model.val_acc}}%</span>
										</div>
										<div class="col-auto">
											<div class="icon icon-shape bg-warning text-white rounded-circle shadow">
												<i class="ni ni-chart-bar-32"></i>
											</div>
										</div>
									</div>
								</div>
						    </div>
						</div>

						<div class="col-md-3">
						    <div class="card card-stats mb-4">
								<div class="card-body">
									<div class="row">
										<div class="col">
											<h5 class="card-title text-muted mb-0">Loss</h5>
											<span class="h2 font-weight-bold mb-0">{{model.loss}}</span>
										</div>
										<div class="col-auto">
											<div class="icon icon-shape bg-yellow text-white rounded-circle shadow">
												<i class="fas fa-chart-bar"></i>
											</div>
										</div>
									</div>
								</div>
						    </div>
						</div>

						<div class="col-md-3">
						    <div class="card card-stats mb-4">
								<div class="card-body">
									<div class="row">
										<div class="col">
											<h5 class="card-title text-muted mb-0">Val_loss</h5>
											<span class="h2 font-weight-bold mb-0">{{model.val_loss}}</span>
										</div>
										<div class="col-auto">
											<div class="icon icon-shape bg-success text-white rounded-circle shadow">
												<i class="ni ni-sound-wave"></i>
											</div>
										</div>
									</div>
								</div>
						    </div>
						</div>
					</div>
				</div>
			</div>
			<div class="row">
				<div class="col-md-12">
					<div class="row">
						<div class="col-md-3">
						    <div class="card card-stats mb-3">
								<div class="card-body">
									<div class="row">
										<div class="col">
											<h5 class="card-title text-muted mb-0">Jumlah Kelas</h5>
											<span class="h2 font-weight-bold mb-0">{{model.jumlah_kelas}}</span>
										</div>
										<div class="col-auto">
											<div class="icon icon-shape bg-info text-white rounded-circle shadow">
												<i class="ni ni-circle-08"></i>
											</div>
										</div>
									</div>
								</div>
						    </div>
						</div>

						<div class="col-md-3">
						    <div class="card card-stats mb-3">
								<div class="card-body">
									<div class="row">
										<div class="col">
											<h5 class="card-title text-muted mb-0">Epoch</h5>
											<span class="h2 font-weight-bold mb-0">{{model.epoch_training}}</span>
										</div>
										<div class="col-auto">
											<div class="icon icon-shape bg-blue text-white rounded-circle shadow">
												<i class="ni ni-user-run"></i>
											</div>
										</div>
									</div>
								</div>
						    </div>
						</div>
						<div class="col-md-3">
						    <div class="card card-stats mb-3">
								<div class="card-body">
									<div class="row">
										<div class="col">
											<h5 class="card-title text-muted mb-0">Waktu Pelatihan</h5>
											<span class="h2 font-weight-bold mb-0">{{model.waktu_training}}</span>
										</div>
										<div class="col-auto">
											<div class="icon icon-shape bg-purple text-white rounded-circle shadow">
												<i class="ni ni-atom"></i>
											</div>
										</div>
									</div>
								</div>
						    </div>
						</div>
						<div class="col-md-3">
						    <div class="card card-stats mb-3">
								<div class="card-body">
									<div class="row">
										<div class="col">
											<h5 class="card-title text-muted mb-0">Dibuat pada</h5>
											<span class="h2 font-weight-bold mb-0"><small>{{model.created_at}}</small></span>
										</div>
										<div class="col-auto">
											<div class="icon icon-shape bg-purple text-white rounded-circle shadow">
												<i class="ni ni-calendar-grid-58"></i>
											</div>
										</div>
									</div>
								</div>
						    </div>
						</div>
					</div>
				</div>
			</div>
			<div class="row mt-2">
				<div class="col-md-6">
				    <div class="card">
				    	<div class="card-header border-0">
				    		<h3 class="mb-0">Grafik Akurasi Pelatihan</h3>
				    	</div>
						<div class="card-body">
							<img :src="acc" style="width: 100%">
						</div>
					</div>
				</div>

				<div class="col-md-6">
				    <div class="card">
				    	<div class="card-header border-0">
				    		<h3 class="mb-0">Grafik Loss Pelatihan</h3>
				    	</div>
						<div class="card-body">
							<img :src="loss" style="width: 100%">
						</div>
					</div>
				</div>
			</div>
			<!-- Footer -->
			<footer class="footer">
			  <div class="row align-items-center justify-content-xl-between">
			    <div class="col-xl-6">
			      <div class="copyright text-center text-xl-left text-muted">
			        &copy; 2020 <a href="" class="font-weight-bold ml-1" target="_blank">Putu Jhonarendra</a>
			      </div>
			    </div>
			  </div>
			</footer>
		</div>
	</div>
</template>
<script>
import axios from 'axios';
export default{
	name:'model',
	data (){
		return{
			title:'Model',
			model:[],
			token:0,
			acc:'',
			loss:''
		}
	},
	methods: {
		load(){
			axios.get(this.$parent.urlApi + 'model.php').then(res => {
				this.model = res.data;
				// console.log(res.data)
			}).catch ((err) => {
				console.log(err);
			}).finally(() => {

			})
		},
		readToken(){
			axios.get(this.$parent.urlApi + 'token.php').then(res => {
				this.token = res.data.training;
				this.acc = 'http://localhost/palmprint/model/'+res.data.training+'_acc.png';
				this.loss = 'http://localhost/palmprint/model/'+res.data.training+'_loss.png';
				// console.log(res.data.training)
			}).catch ((err) => {
				console.log(err);
			}).finally(() => {

			})
		}
	},
	mounted(){
		this.load();
		this.readToken();
		this.$parent.headerTitle = this.title;
		this.$parent.routerPath = this.$route.name;
		document.title = this.title + ' | ' + this.$parent.title;
	}
}
</script>
<style scoped>
 
</style>


