<script>

let axios = require('axios')
export default {
  name: 'palmprint-identification',
  data (){
   return{
    title:'Palmprint Identification',
    headerTitle: '',
    urlApi:'http://localhost/palmprint/api/web/',
    // urlApi:'http://jhon98.000webhostapp.com/api/',
    routerPath: '',
    notif:0,
    notif_c:0,
    notifElement:true,
    notifElementC:true,

    showMobileNav: false,
   }
  },
  watch: {
    '$route.name' (newVal) {
      this.showMobileNav = false
    }
  },
  methods:{
    enableTensorflow(){
	     this.notif = 1;
      axios.get(this.urlApi + 'start_tensorflow.php').then(res => {
        this.notif = 2;	
        setTimeout(() => this.notifElement = false, 4000);
      }).catch ((err) => {
        console.log(err);
      }).finally(() => {
        
      })
    },
    clearCache(){
      this.notif_c = 1;
      axios.get(this.urlApi + 'clear_cache.php').then(res => {
        this.notif_c = 2; 
        setTimeout(() => this.notifElementC = false, 4000);
      }).catch ((err) => {
        console.log(err);
      }).finally(() => {
        
      })
    }  
  },
  mounted(){
    document.title = this.title;
  }
}
</script>
